import React, { useState, useEffect } from 'react';
import { BaseUrl, ApiBaseUrl } from '../config.js';
import TopBar from '../components/HomePage/TopBar';
import '../css/BlogsPage.css'

function BlogsRow(props){
    const blogRowContainerStyle = {
        margin: "10px 0px",
        overflow: "scroll",
        cursor: "pointer",
    }
    const blogTitleStyle = {
        margin: "5px 0px",
        textAlign: "left",
        fontFamily: "Exo",
        fontStyle: "normal",
        fontWeight: "400",
        lineHeight: "normal",
    }
    const blogDescriptionStyle = {
        margin: "5px 0px",
        textAlign: "left",
        color: "#000",
        fontFamily: "Exo 2",
        fontStyle: "normal",
        fontWeight: "400",
        lineHeight: "normal",
    }

    return (
        <div id="blogs-row-container" style={blogRowContainerStyle} onClick={() => window.open(props.link, '_blank')}>
            <h2 id="blog-row-title" style={blogTitleStyle}>{props.title}</h2>
            <h3 id="blog-row-description" style={blogDescriptionStyle}>{props.description}</h3>
        </div>
    )
}

function BlogsPage(){
    const [blogs, setBlogs] = useState(null);
    const [pageNumber, setPageNumber] = useState(0);
    const [totalPages, setTotalPages] = useState(0);

    useEffect(() => {

        fetch(ApiBaseUrl+'blogs-page/' + pageNumber)
        .then(response => response.json())
        .then(data => {
            setBlogs(data.blogs); 
            setTotalPages(data.total_pages);
        })
        .catch(error => {
        console.error('Error fetching data:', error);
        });

    }, [pageNumber]);

    if (blogs == null){
        return (<div> Loading . . .</div>)
    } 

    const topBarStyle = {
        backgroundColor: "#fff",
    }

    const blogsContainerStyle = {
        width: "100vw",
        marginBottom: "100px",
        display: "flex",
        flexDirection: "column",
        judtifyContent: "center",
        alignItems: "center",
    }
    return(
        <div>  
            <div style={topBarStyle}>
            <TopBar  />
            </div>
            <div id="blogs-container" style={blogsContainerStyle}>
                {
                    Object.keys(blogs).map((key) => {
                        return (
                            <BlogsRow
                                key={blogs[key].time_stamp}
                                link={BaseUrl+'blogs/'+blogs[key].blog_id}
                                title={blogs[key].blog_title}
                                description={blogs[key].blog_description}
                            />
                        )
                    })
                }

            </div>
            <div id="pagination-container">
                <button className='pagination-button' onClick={() => setPageNumber(pageNumber-1)} disabled={pageNumber === 0}>Previous</button>
                <h3 id='page-counter' >({pageNumber+1} / {totalPages} )</h3>
                <button className='pagination-button' onClick={() => setPageNumber(pageNumber+1)} disabled={(pageNumber+1) === totalPages}>Next</button>
            </div>
        </div>
    )
}

export default BlogsPage;